body {
    background-image: url('/assets/images/background.jpg');
    background-size: 100%;
    background-position: 20% 20%;
}

.main-div {
    position: fixed;
    width: 100%;
    height: 85%;
    padding: 0;
    margin: 0;
    color: $light;
}

.bottom-navbar {
    position: fixed;
    top: 85%;
    width: 100%;
    height: 15%;
    background-color: rgba($light, 0.9);
    padding: 2.5%;
    margin: 0;
}

.sidelink {
    display: block;
    text-decoration: none;
    float: left;
    width: 33%;
    height: 40%;
    text-align: center;
    color: $dark;
    font-family: 'Lato-bold';
    font-size: 150%;
}

.sidelink:hover {
    color: $accent;
}

body {
    padding: 0;
    margin: 0;
}